@use 'base/variables'as v;
@use 'base/mixins'as m;

.acciones {
    margin-top: v.$separacion;
    min-width: 80%;
    max-width: 100%;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    a {
        color: v.$azul;
        display: block;
        margin-bottom: calc(v.$separacion/3);
        text-align: center;
        margin-left: 2rem;
        margin-right: 2rem;

        &:hover {
        color: lighten(v.$azul, 50%);
        font-size: 1.7rem;
        transition: 90ms;
        }
    }
}