@use 'base/variables'as v;
@use 'base/mixins'as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/logocolpomed.jpg);
        width: 100%;
        background-size: cover;
        background-position: center center;

        @include m.tablet {
            height: 100%;
            background-size: cover;
            background-position: top;
            background-color: #cee2e9;
        }
    }

    .app {
        width: 95%;
        @include m.tablet{
            overflow: scroll;
        }
        margin: 0 auto;
        padding: 3rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.barra {
    width: 100%; 
    p{
        text-align: left;
    }

    @include m.desktop{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .boton {
        @include m.boton(v.$rojo, v.$blanco);
        padding: 1rem 2rem;
        &:hover {
            background-color: darken(v.$rojo, 10%);
        }
    }
}

.barra-servicios{
    width: 100%;
    margin-bottom: 3rem;

    @include m.desktop{
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    .servi{
        margin: 0 0 5px 0;
        @include m.desktop{
            margin:0 5px 0 0;
            flex-grow: 1;

            &:last-of-type{
            margin:0 0 0 0;

            }
        }

    }
}