@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta{
    padding: calc(v.$separacion/4);
    color: v.$blanco;
    width: 90%;
    display: block;
    text-align: center;
    border-radius: 1rem;
    margin-bottom: 1.2rem;
    font-weight: v.$bold;
    text-transform: uppercase;
    &.error{
        background-color: v.$rojo;
    }
    &.exito {
        background-color: v.$verde;
    }
}