/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: rgba(26, 27, 21, 0.8666666667);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

ontenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: rgba(26, 27, 21, 0.8666666667);
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: rgba(26, 27, 21, 0.8666666667);
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/logocolpomed.jpg);
  width: 100%;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: 100%;
    background-size: cover;
    background-position: top;
    background-color: #cee2e9;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  width: 100%;
}
.barra p {
  text-align: left;
}
@media (min-width: 1024px) {
  .barra {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.barra .boton {
  background-color: #cb0000;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #ff6565;
  cursor: pointer;
}
.barra .boton:hover {
  background-color: #980000;
}

.barra-servicios {
  width: 100%;
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .barra-servicios {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}
.barra-servicios .servi {
  margin: 0 0 5px 0;
}
@media (min-width: 1024px) {
  .barra-servicios .servi {
    margin: 0 5px 0 0;
    flex-grow: 1;
  }
  .barra-servicios .servi:last-of-type {
    margin: 0 0 0 0;
  }
}

.seccion {
  display: none;
  padding: 2rem 0;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  color: #fFF;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  border: 1px solid #002d7b;
  border-radius: 8px;
  padding: 1.5rem;
  background-color: rgba(26, 27, 21, 0.8666666667);
  transition-property: transform;
  transition-duration: 300ms;
  margin-top: 1rem;
  cursor: pointer;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio.seleccionado {
  background-color: #002d7b;
  transform: scale(1.1);
  margin-top: 1rem;
}
.listado-servicios .descripcion-servicio {
  font-size: 12px;
  text-align: justify;
  color: #FFFFFF;
}
.listado-servicios .contenedor-descripcion {
  margin-bottom: 1rem;
  height: 25%;
}
.listado-servicios .imagen-servicio {
  max-height: 45%;
  width: 100%;
  border-radius: 10px;
}
.listado-servicios .contenedor-nombre {
  text-align: center;
  min-height: 46px;
}
.listado-servicios .nombre-servicio {
  font-weight: bold;
  color: #FFFFFF;
}

.w-100 {
  width: 100% !important;
  margin: 3rem 0 -30px 0 !important;
}

.ocultar2 {
  visibility: hidden;
}

.mostrara {
  border: 1px solid #002d7b;
  border-radius: 8px;
  padding: 2rem 1rem 2rem 2rem;
  text-align: left;
}
@media (min-width: 1024px) {
  .mostrara {
    padding: 2rem 1rem 2rem 10rem;
  }
}
.mostrara label {
  min-width: 14rem !important;
  display: inline-flex;
  flex-direction: row;
}

.div-boton {
  display: flex;
  justify-content: center;
}
.div-boton .boton {
  background-color: #329f00;
  transition-property: transform;
  transition: transform 400ms;
}
.div-boton .boton:hover {
  background-color: #42d200;
  transform: scale(1.1);
}

.imagen-cita {
  position: relative;
  min-width: 350px;
  max-width: 350px;
  margin: 5rem 0 !important;
  width: 100%;
}
.imagen-cita:first-of-type {
  margin-top: 2rem !important;
  margin-bottom: 5rem !important;
}
.imagen-cita img {
  filter: brightness(1);
  border-radius: 10px;
}
@media (min-width: 1024px) {
  .imagen-cita {
    width: auto;
    margin: 0 !important;
  }
  .imagen-cita:first-of-type {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
}

.div-cita {
  background-color: rgba(26, 27, 21, 0.8666666667);
  position: absolute;
  min-width: 350px;
  max-width: 350px;
  top: 0px;
  left: 0px;
  padding: 1rem 3rem;
  border-radius: 10px;
}
.div-cita p {
  color: #FFFFFF;
}

.citas-admin {
  display: flex;
  flex-direction: column !important;
}
@media (min-width: 1024px) {
  .citas-admin {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.5rem;
  }
}

h3 {
  margin-bottom: 0rem;
}
@media (min-width: 1024px) {
  h3 {
    margin-bottom: 2rem;
  }
}

.admin-servicio .servicio {
  border: 1px solid #002d7b;
  border-radius: 8px;
  padding: 1.5rem;
  background-color: rgba(26, 27, 21, 0.8666666667);
  transition-property: transform;
  transition-duration: 300ms;
  margin-top: 1rem;
  cursor: auto;
}
.admin-servicio .servicio:hover {
  transform: scale(1);
}
.admin-servicio .servicio .contenedor-descripcion {
  margin-bottom: 1rem;
  height: 20%;
}
.admin-servicio .servicio .imagen-servicio {
  max-height: 45%;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 2rem;
}
.admin-servicio .servicio .botones {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.admin-servicio .servicio .botones .boton {
  width: 145px;
  margin-bottom: 5rem;
  padding: 0.7rem 0.5rem;
}
.admin-servicio .servicio .botones .editar {
  background-color: #ffc107;
}
.admin-servicio .servicio .botones .eliminar {
  background-color: #cb0000;
}

.formulario {
  margin-top: 4rem;
  width: 100%;
  align-items: center;
  padding: 4rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 12rem;
  color: #FFFFFF;
}
.campo input {
  width: 100%;
  border: none;
  border-radius: 1rem;
  padding: 1.25rem;
}
.campo input:disabled {
  background-color: rgba(184, 187, 167, 0.8666666667);
  cursor: not-allowed;
  text-transform: uppercase;
}

.contenedor-formulario {
  min-width: 90%;
  max-width: 100%;
  border-radius: 1rem;
  background-color: rgba(26, 27, 21, 0.8666666667);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.32);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.32);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.32);
}

.campo input:focus {
  border: solid 3px blue;
}

.boton {
  background-color: #002d7b;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #156bff;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
  min-width: 80%;
  max-width: 100%;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #002d7b;
  display: block;
  margin-bottom: 1.6666666667rem;
  text-align: center;
  margin-left: 2rem;
  margin-right: 2rem;
}
.acciones a:hover {
  color: #7babff;
  font-size: 1.7rem;
  transition: 90ms;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 90%;
  display: block;
  text-align: center;
  border-radius: 1rem;
  margin-bottom: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #002d7b;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 8px;
  transition: background-color 400ms ease;
}
.tabs button.actual {
  background-color: #156bff;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #156bff;
}
.tabs button:last-child {
  margin-bottom: 0px;
}
@media (min-width: 768px) {
  .tabs button:last-child {
    margin-bottom: 8px;
  }
}

.paginacion {
  padding: 2rem;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.mostrar {
  visibility: visible;
}