@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    display: grid;
    color:#fFF;
    gap: 2rem;
    @include m.desktop{
         @include m.grid(2,2rem)
    }
    .servicio{
        border:1px solid v.$azul ;
        border-radius:8px;
        padding: 1.5rem;
        background-color: v.$negro;
        transition-property: transform;
        transition-duration: 300ms;
        margin-top:1rem;
        cursor: pointer;
        &:hover{
            transform: scale(1.1);
        }
        &.seleccionado{
            background-color: v.$azul;
            transform: scale(1.1);
            margin-top:1rem;
        }
        
    }
    .descripcion-servicio{
        font-size: 12px;
        text-align: justify;
        color:v.$blanco;
    }
    .contenedor-descripcion{
        margin-bottom: 1rem;
        height: 25%;
    
    }
    
    .imagen-servicio{
        max-height: 45%;
        width: 100%;
        border-radius: 10px;
    }

    .contenedor-nombre{
        text-align: center;
        min-height: 46px;
    }
    .nombre-servicio{
       font-weight: bold;
        color:v.$blanco;
    }
}

.w-100{
    width: 100% !important;
    margin:3rem 0 -30px 0 !important;
}

.ocultar2{
    visibility: hidden;
}

.mostrara{
    border:1px solid v.$azul;
    border-radius: 8px;
    padding: 2rem 1rem 2rem 2rem; 
    text-align: left;
    @include m.desktop{
        padding: 2rem 1rem 2rem 10rem;   
    }
    label{
       min-width: 14rem !important;
       display: inline-flex;
       flex-direction: row;
    }
}
.div-boton{
    display: flex;
        justify-content: center;
    .boton{
        background-color: v.$verde;
        transition-property: transform;
        transition: transform 400ms;
        &:hover{
        background-color:lighten(v.$verde, 10%);
        transform: scale(1.1);
        }
    }
}