@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 4rem;
    width: 100%;
    align-items: center;
    padding: 4rem;
}
.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 12rem;
        color: v.$blanco;
    }
    input{
        width: 100%;
        //border-color: v.$azul ;
        border: none;
        border-radius: 1rem;
        padding: calc(v.$separacion/4);
        &:disabled{
            background-color: lighten(v.$negro,60%);
            cursor:not-allowed;
            text-transform:uppercase;
        }
    };
}

.contenedor-formulario{
    // border:2px solid v.$azul;
    min-width: 90%;
    max-width: 100%;
    border-radius: 1rem;
    //border-color: v.$negro;
    background-color: v.$negro;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.32);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.32);
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.32);
    // background: -prefix-linear-gradient(top, v.$negro, v.$gris);
    // background: linear-gradient(to bottom,v.$negro, v.$gris);
}

.campo input:focus{
    border: solid 3px blue;
    }