
@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs{
    @include m.tablet{
        display: flex;
    }
    
    button{
        background-color: v.$azul;
        border: none;
        color:v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        font-weight: v.$bold;
        text-transform: uppercase;
        margin-bottom: 8px;
        transition: background-color 400ms ease;

        &.actual{
            background-color: lighten(v.$azul,30%);
        }

        &:hover{
            cursor: pointer;
            background-color: lighten(v.$azul,30%);
        }
        &:last-child{
            margin-bottom: 0px;
         }
         @include m.tablet{
            &:last-child{
                margin-bottom:8px ;}
         }
    }
}