@use 'base/mixins' as m;
@use 'base/variables' as v;



.imagen-cita{
    position: relative;
    min-width: 350px;
    max-width: 350px;
    margin: 5rem 0 !important;

    &:first-of-type{
        margin-top: 2rem !important;;
        margin-bottom: 5rem !important;;
    }
    img{
        filter: brightness(1);
        border-radius: 10px;
    }
    width: 100%;
    @include m.desktop{
        width: auto;
        margin: 0 !important;
        &:first-of-type{
            margin-top: 0 !important;;
            margin-bottom: 0 !important;;
        }
    }
}

.div-cita{
    background-color: v.$negro;
    position:absolute;
    min-width: 350px;
    max-width: 350px;

    top: 0px;
    left: 0px;
    padding: 1rem 3rem;
    border-radius: 10px;
    p{
        color: v.$blanco;
    }
}

.citas-admin{
    display: flex;
    flex-direction: column !important;

    @include m.desktop{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        column-gap: 1.5rem;
    }
}

h3{
    margin-bottom: 0rem;
    @include m.desktop{
        margin-bottom: 2rem;
    }
}

.admin-servicio{

    .servicio{
        border:1px solid v.$azul ;
        border-radius:8px;
        padding: 1.5rem;
        background-color: v.$negro;
        transition-property: transform;
        transition-duration: 300ms;
        margin-top:1rem;
        cursor: auto;
        &:hover{
            transform: scale(1);
        }
        .contenedor-descripcion{
            margin-bottom: 1rem;
            height: 20%;
        }
        .imagen-servicio{
            max-height: 45%;
            width: 100%;
            border-radius: 10px;
            margin-bottom: 2rem;
        }
        

        .botones{
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;
            

            .boton{
               width: 145px;
               margin-bottom: 5rem;
               padding: 0.7rem 0.5rem ;
            }
            .editar{
                background-color: v.$amarillo;
            }
            .eliminar{
                background-color: v.$rojo;

            }
        }
        
    }
}